  display: grid;
  place-content: center;
}

.game-over-info-panel {
  background-color: rgba(0, 0, 0, 0.733);
  border-radius: 25px;
  padding: 2rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.game-over-message {
  font-family: Monserrat, Arial, Helvetica, sans-serif;
  font-size: 2.5rem;
  font-weight: bold;
  color: white;
}

.play-again-button {
  font-family: Monserrat, Arial, Helvetica, sans-serif;
  font-size: 2rem;
  font-weight: bold;
  border: none;
  border-radius: 25px;
  padding: 0.5rem 1.5rem;
}
