  width: 80vh;
}

.gameboard {
  background-color: rgb(54, 115, 172);
  height: 80vh;

  border: 5px solid black;

  display: grid;
  grid-template: repeat(10, 1fr) / repeat(10, 1fr);
}

.gameboard-status-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 6rem;
}

.gameboard-status-message {
  font-size: 2rem;
  font-style: italic;
  font-weight: bold;
}
