___CSS_LOADER_EXPORT___.push([module.id, `.gameboardCell:not(.ship) {
  border: 1px solid rgb(71, 148, 184);
  cursor: pointer;
}

.ship {
  background-color: black;
}

.horizontal-ship {
  border-top: 5px solid grey;
  border-bottom: 5px solid grey;
}

.horizontal-ship-head {
  border-left: 10px solid rgb(34, 74, 92);
}

.horizontal-ship-tail {
  border-right: 10px solid rgb(34, 74, 92);
}

.vertical-ship {
  border-left: 5px solid grey;
  border-right: 5px solid grey;
}

.vertical-ship-head {
  border-top: 10px solid rgb(34, 74, 92);
}

.vertical-ship-tail {
  border-bottom: 10px solid rgb(34, 74, 92);
}

.hit {
  background-color: red;
}

.miss {
  background-color: white;
}

/* Remove blue borders around edge of gameboard */
.gameboardCell:not(.ship):nth-child(10n - 9) {
  border-left: none;
}

.gameboardCell:not(.ship):nth-child(10n) {
  border-right: none;
}

.gameboardCell:not(.ship):nth-child(n + 91) {
  border-bottom: none;
}

.gameboardCell:not(.ship):nth-child(-n + 10) {
  border-top: none;
}
